// Types
type ViewType = 'canvas' | 'social' | 'messages' | 'dashboard'
type CardType = 'stat' | 'product' | 'vendor' | 'service' | 'auction' | 'advert' | 'logistics' | 'event' | 'chat' | 'storefront'

interface User {
  id: string
  name: string
  avatar: string
  online: boolean
  lastSeen?: string
}

interface Message {
  id: string
  senderId: string
  text: string
  timestamp: Date
  read: boolean
}

interface Chat {
  id: string
  name: string
  avatar: string
  isGroup: boolean
  lastMessage: string
  lastMessageTime: string
  unread: number
  online: boolean
  participants?: User[]
  messages: Message[]
}

interface Card {
  id: string
  type: CardType
  title: string
  subtitle?: string
  image?: string
  price?: string
  originalPrice?: string
  rating?: number
  reviews?: number
  category?: string
  trend?: string
  value?: string
  status?: string
  progress?: number
  countdown?: string
  bids?: number
  currentBid?: string
  location?: string
  date?: string
  attendees?: number
  description?: string
  tags?: string[]
  vendor?: {
    name: string
    avatar: string
    verified: boolean
  }
  tracking?: {
    status: string
    location: string
    eta: string
    events: { time: string; location: string; status: string }[]
  }
}

interface SidebarItem {
  id: string
  name: string
  sub: string
  icon?: string
  img?: string
  time?: string
  unread?: number
  online?: boolean
}

interface Post {
  id: string
  user: User
  content: string
  image?: string
  likes: number
  comments: number
  shares: number
  timestamp: string
  liked: boolean
}

