// Star Rating Component
const StarRating = ({ rating, size = 'sm' }: { rating: number; size?: 'sm' | 'md' }) => {
  const sizeClass = size === 'sm' ? 'w-3 h-3' : 'w-4 h-4'
  return (
    <div className="flex items-center gap-0.5">
      {[1, 2, 3, 4, 5].map((star) => (
        <svg
          key={star}
          className={`${sizeClass} ${star <= rating ? 'text-[#C5A059]' : 'text-gray-300'}`}
          fill="currentColor"
          viewBox="0 0 20 20"
        >
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      ))}
    </div>
  )
}

// Online Status Indicator
const OnlineIndicator = ({ online, size = 'md' }: { online: boolean; size?: 'sm' | 'md' | 'lg' }) => {
  const sizeClass = size === 'sm' ? 'w-2 h-2' : size === 'md' ? 'w-3 h-3' : 'w-4 h-4'
  return (
    <span
      className={`${sizeClass} rounded-full ${online ? 'bg-green-500 pulse-online' : 'bg-gray-400'} inline-block`}
    />
  )
}

// Card Components
const StatCard = ({ card }: { card: Card }) => (
  <div className="card-base p-6 bg-[#111b21] rounded-[24px] text-white shadow-xl">
    <div className="flex justify-between items-start mb-6">
      <span className="text-[10px] font-black text-emerald-400 tracking-widest uppercase">{card.title}</span>
      <button className="text-gray-500 hover:text-white transition-colors">
        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeWidth="2" d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
        </svg>
      </button>
    </div>
    <div className="flex items-end gap-3">
      <h4 className="text-4xl font-extrabold">{card.value}</h4>
      <span className="text-emerald-400 text-sm font-bold mb-1">{card.trend}</span>
    </div>
    <p className="text-gray-400 text-sm mt-2">{card.subtitle}</p>
  </div>
)

const ProductCard = ({ card, onClick }: { card: Card; onClick: () => void }) => (
  <div className="card-base bg-white rounded-[24px] overflow-hidden border border-gray-100 shadow-sm flex flex-col cursor-pointer" onClick={onClick}>
    <div className="relative">
      <img src={card.image} alt={card.title} className="h-48 w-full object-cover" />
      {card.originalPrice && (
        <span className="absolute top-3 left-3 bg-red-500 text-white text-[10px] font-black px-2 py-1 rounded-lg">
          SALE
        </span>
      )}
      {card.vendor?.verified && (
        <span className="absolute top-3 right-3 bg-white/90 backdrop-blur-sm text-emerald-600 text-[10px] font-bold px-2 py-1 rounded-lg flex items-center gap-1">
          <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M6.267 3.455a3.066 3.066 0 001.745-.723 3.066 3.066 0 013.976 0 3.066 3.066 0 001.745.723 3.066 3.066 0 012.812 2.812c.051.643.304 1.254.723 1.745a3.066 3.066 0 010 3.976 3.066 3.066 0 00-.723 1.745 3.066 3.066 0 01-2.812 2.812 3.066 3.066 0 00-1.745.723 3.066 3.066 0 01-3.976 0 3.066 3.066 0 00-1.745-.723 3.066 3.066 0 01-2.812-2.812 3.066 3.066 0 00-.723-1.745 3.066 3.066 0 010-3.976 3.066 3.066 0 00.723-1.745 3.066 3.066 0 012.812-2.812zm7.44 5.252a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
          </svg>
          Verified
        </span>
      )}
    </div>
    <div className="p-5 flex-1 flex flex-col">
      <span className="text-[10px] text-gray-400 font-bold uppercase tracking-wider mb-1">{card.category}</span>
      <h4 className="font-bold text-gray-800 mb-2 line-clamp-2">{card.title}</h4>
      <div className="flex items-center gap-2 mb-2">
        <StarRating rating={card.rating || 0} />
        <span className="text-xs text-gray-500">({card.reviews})</span>
      </div>
      <div className="mt-auto flex items-center gap-2">
        <span className="text-emerald-600 font-extrabold text-xl">{card.price}</span>
        {card.originalPrice && (
          <span className="text-gray-400 line-through text-sm">{card.originalPrice}</span>
        )}
      </div>
      <div className="flex gap-2 mt-4">
        <button className="flex-1 bg-gray-100 py-2.5 rounded-xl text-xs font-bold hover:bg-gray-200 transition-all">
          Preview
        </button>
        <button className="bg-[#111b21] text-white px-4 py-2.5 rounded-xl hover:bg-emerald-600 transition-all">
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeWidth="2" d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
          </svg>
        </button>
      </div>
    </div>
  </div>
)

const AuctionCard = ({ card, onClick }: { card: Card; onClick: () => void }) => (
  <div className="card-base bg-white rounded-[24px] overflow-hidden border-2 border-orange-400 shadow-lg cursor-pointer" onClick={onClick}>
    <div className="relative">
      <img src={card.image} alt={card.title} className="h-48 w-full object-cover" />
      <div className="absolute top-3 left-3 bg-orange-500 text-white text-[10px] font-black px-3 py-1.5 rounded-lg flex items-center gap-1 countdown-pulse">
        <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
        </svg>
        {card.countdown}
      </div>
      <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/60 to-transparent p-4">
        <span className="text-white/80 text-xs font-medium">{card.bids} bids</span>
      </div>
    </div>
    <div className="p-5">
      <span className="text-[10px] text-orange-500 font-bold uppercase tracking-wider mb-1 block">Live Auction</span>
      <h4 className="font-bold text-gray-800 mb-2 line-clamp-2">{card.title}</h4>
      <div className="flex justify-between items-end">
        <div>
          <span className="text-gray-500 text-xs">Current Bid</span>
          <p className="text-orange-600 font-extrabold text-2xl">{card.currentBid}</p>
        </div>
        <button className="bg-orange-500 text-white px-4 py-2.5 rounded-xl text-xs font-bold hover:bg-orange-600 transition-all bid-glow">
          Place Bid
        </button>
      </div>
    </div>
  </div>
)

